import { BaseSDK } from "../common/BaseSDK";
import { HttpMethod } from "../common/HttpMethod";
import { RestInput } from "../datasource/rest/RestInput";
import { FetchTranslationsInput } from "./common/FetchTranslationsInput";
import TranslationProxySDKConfig from "./TranslationSDKConfig.proxy.sdk";

/**
 * TranslationService is the Service responsible for translation.
 * Current implmenetation supports SalesForce Custom Labels and Dictionaries.
 * TranslationProxy SDK is to interact with Vlocity Translation SDK functionality hosted in node server.
 */
export default class TranslationProxy extends BaseSDK {
  /**
   * Get a singleton instance of this Translation.
   * @param {config} TranslationProxySDKConfig to initialize this SDK.
   * @returns an instance of this SDK.
   */
  public static getInstance(
    config: TranslationProxySDKConfig
  ): TranslationProxy {
    // If there is no instance and no datasource to create the instance, throw an error.
    if (
      !TranslationProxy.instance &&
      (!config || !config.datasource || !config.secureServerUrl)
    ) {
      throw new Error(
        "TranslationService:getInstance: TranslationService cannot be instantiated properly. TranslationProxySDKConfig, TranslationProxySDKConfig.secureServerUrl and TranslationProxySDKConfig.datasource must be provided."
      );
    }
    if (!TranslationProxy.instance || (config && config.create)) {
      TranslationProxy.instance = new TranslationProxy(config);
    }
    return TranslationProxy.instance;
  }

  private static instance: TranslationProxy;

  /**
   * The config used to initialize this SDK.
   */
  private config: TranslationProxySDKConfig;

  private translatedLabels: object;

  constructor(config: TranslationProxySDKConfig) {
    super();
    this.config = config;
  }

  /**
   * Create an input object of the FetchTranslationInput.
   * @returns {FetchTranslationInput} - input object that contains the labels needs to be fetched.
   */
  public createFetchTranslationsInput(): FetchTranslationsInput {
    return new FetchTranslationsInput();
  }

  /**
   * This function fetch the translations for a given set of label(s) with the language provided.
   * @param {FetchTranslationsInput} - input object that contains the labels to be translated and the language to translate to.
   * @return {Promise<Result>} - promise that will get all the translated labels and keep them in labelStorage.
   */
  public async fetchTranslations(input: FetchTranslationsInput): Promise<any> {
    if (!input.secureServerEndPointUrl) {
      input.secureServerEndPointUrl = "/fetchtranslations";
    }
    const restURL = this.config.secureServerUrl + input.secureServerEndPointUrl;
    return await this.invokeAPI(HttpMethod.POST, restURL, input).then(
      async result => {
        if (!result) {
          throw new Error("Invalid result: " + result);
        }
        this.translatedLabels = result;
      }
    );
  }

  /**
   * Get the corresponding translated string for a given label.
   * fetch() must be called first before calling this function.
   * This function translates only strings exist in translatedLabels. Otherwise, it will return the provided label.
   * @param {label} - to be translated.
   * @returns {string} - translatedLabel that is translated if exists. Otherwise, return the provided label.
   */
  public translate(label: string): string {
    if (this.translatedLabels) {
      return this.translatedLabels[label] || label;
    }
    // return original label if translation not found.
    return label;
  }

  private async invokeAPI(
    httpMethod: HttpMethod,
    restURL: string,
    input?: FetchTranslationsInput
  ) {
    let restOptions: RestInput;
    restOptions = new RestInput({
      options: {
        body: JSON.stringify({ input: JSON.stringify(input) }),
        cacheExpiration: this.config.cacheExpiration,
        cacheable: this.config.cacheable,
        headers: {
          "Content-Type": input.contentType
        },
        method: httpMethod
      },
      reload: this.config.reload,
      url: restURL
    });
    return await this.config.datasource.rest(restOptions).execute();
  }
}
